import {CalendarEventColor} from './CalendarEventColor';
import { StringUtils } from '@allianzSND/core';

export class CalendarEventDetail {
  private _clientID: string;
  private _calendarID: string;
  private _title: string;
  private _location: string;
  private _activity :string;
  private _isAllDay: boolean;
  private _start: Date;
  private _end: Date;
  private _isAlert: boolean;
  private _alert1: string;
  private _alert2: string;
  private _alert3: string;
  private _remark: string;
  private _color;
  private _displayActivity: string;
  private _displayAlert1: string;
  private _displayAlert2: string;

  constructor(clientID: string,calendarID: string, title: string, location: string, calendarType: string,isAllDay: string, start: Date, end: Date, isAlert: string, alert1: string, alert2: string, alert3: string, remark: string, color: string) {
    this._clientID = clientID;
    this._calendarID = calendarID;
    this._title = title;
    this._location = location;
    this._activity = calendarType;
    this._isAllDay = (isAllDay == 'Y');
    this._start = new Date(start);
    this._end = new Date(end);
    this._isAlert = (isAlert == 'Y');
    this._alert1 = alert1;
    this._alert2 = alert2;
    this._alert3 = alert3;
    this._remark = remark;


    if(StringUtils.isEmpty(this._activity)) this._activity = null;
    if(StringUtils.isEmpty(this._title)) this._title = '';
    if(StringUtils.isEmpty(this._location)) this._location = '';
    if(StringUtils.isEmpty(this._alert1)) this._alert1 = '';
    if(StringUtils.isEmpty(this._alert2)) this._alert2 = null;
    if(StringUtils.isEmpty(this._alert3)) this._alert3 = '';
    if(StringUtils.isEmpty(this._remark)) this._remark = '';

    let eventColor = JSON.parse(color);
    if(eventColor != null)
      this._color = new CalendarEventColor(eventColor.color, eventColor.backgroundColor);
  }

  set clientID(clientID: string) {
    this._clientID = clientID;
  }

  set calendarID(calendarID: string) {
    this._calendarID = calendarID
  }

  set title(title: string) {
    this._title = title;
  }

  set location(location: string) {
    this._location = location;
  }

  set activity(calendarType: string) {
    this._activity = calendarType;
  }

  set allDay(isAllDay : boolean) {
    this._isAllDay = isAllDay;
  }

  set start(date: Date) {
    this._start = date;
  }

  set end(date: Date) {
    this._end = date;
  }

  set isAlert(isAlert: boolean) {
    this._isAlert = isAlert;
  }

  set alert1(alert1: string) {
    this._alert1 = alert1;
  }

  set alert2(alert2: string) {
    this._alert2 = alert2;
  }

  set alert3(alert3: string) {
    this._alert3 = alert3;
  }

  set remark(remark: string) {
    this._remark =  remark;
  }

  set color(color: string) {
    let eventColor = JSON.parse(color);
    if(eventColor != null)
      this._color = new CalendarEventColor(eventColor.color, eventColor.backgroundColor);
  }

  set displayActivity(displayActivity : string) {
    this._displayActivity = displayActivity;
  }

  set displayAlert1(displayAlert1 : string) {
    this._displayAlert1 = displayAlert1;
  }

  set displayAlert2(displayAlert2 : string) {
    this._displayAlert2 = displayAlert2;
  }

  get clientID() {
    return this._clientID;
  }

  get calendarID() {
    return this._calendarID;
  }

  get title() {
    return this._title;
  }

  get location() {
    return this._location;
  }

  get activity() {
    return this._activity;
  }

  get allDay() {
    return this._isAllDay;
  }

  get start() {
    return this._start;
  }

  get end() {
    return this._end;
  }

  get isAlert() {
    return this._isAlert;
  }

  get alert1() {
    return this._alert1;
  }

  get alert2() {
    return this._alert2;
  }

  get alert3() {
    return this._alert3;
  }

  get remark() {
    return this._remark;
  }

  get color() {
    return this._color;
  }

  get displayActivity() {
    return this._displayActivity;
  }

  get displayAlert1() {
    return this._displayAlert1;
  }

  get displayAlert2() {
    return this._displayAlert2;
  }
}
