import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ProfileCode} from '@allianzSND/core';

@Component({
  selector: 'snd-calendar-filter',
  templateUrl: './calendar-filter.component.html',
  styleUrls: ['./calendar-filter.component.scss']
})
export class CalendarFilterComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  private _optionMap : Map<string, Array<ProfileCode>> = new Map<string, Array<ProfileCode>>();
  public activityTypeList: Array<ProfileCode> = []; // DBactivityType
  public currentFilterOptionList: Array<string> = [];

  @Input()
  get optionMap() {return this._optionMap}
  set optionMap(optionMap : Map<string, Array<ProfileCode>>) {
    this._optionMap = optionMap;
    this.activityTypeList = this._optionMap.get('Calendar_Type');
    this.activityTypeList.forEach((codeItem) => {
      if (codeItem.isCheck) {
        this.currentFilterOptionList.push(codeItem.getCode());
      }
    });
  }

  @Output() activityTypeListChange = new EventEmitter();

  onActivityFilter() {
    this.activityTypeListChange.emit(this.activityTypeList);
  } // filter event

  toActivityColor(item: ProfileCode) {
    return JSON.parse(item.getArguments()).color;
  }
}
